.btnGroupWrap {
  display: grid;
  justify-content: center;
  align-content: center;
  border-radius: 0.4rem;
  grid-template-columns: repeat(auto-fit, minmax(6rem, 1fr));
  grid-gap: 0.5rem;
}
.btnGroupWrap button {
  background: var(--blue);
  border: none;
  min-height: 2rem;
  padding: 0.2rem 0.4rem;
  border-radius: 0.4rem;
  color: var(--white);
  font-weight: 700;
  font-size: 0.6rem;
  cursor: pointer;
  outline-color: var(--purple);
}